import {Component,OnInit} from '@angular/core';
import {Router,ActivatedRoute,Params} from '@angular/router';
import { User } from'../../models/user';
import {GLOBAL} from '../../services/global';
import {UserService} from '../../services/user.service';
import {UploadService} from '../../services/upload.service';
import { Assignment } from'../../models/assignment';
import {AssignmentService} from '../../services/assignment.service';


@Component({
	selector:'register',
	templateUrl:'./register.component.html',
	styleUrls: ['./register.component.css'],
	providers: [UserService,UploadService,AssignmentService]
})

export class RegisterComponent implements OnInit{
	public title:String;
	public user:User;
	public message:string;
	public filesToUpload: Array<File>;
	public url:string;
	public assignment_name:string;
	public filesToUploadCatalogProfesor: Array<File>;
	public assignment;
	public clave_assignment:string;

	constructor(
		private _route:ActivatedRoute,
		private _router:Router,
		private _userService:UserService,
		private _uploadService:UploadService,
		private _assignmentService:AssignmentService

	){
		this.title='Registro';
		this.user= new User('','','','','','','','','');
		this.url=GLOBAL.url;

	}

	ngOnInit(){
		console.log('Registro cargado');
	}
	onSubmit(registerForm){
		this.message='wait';
		//this.user.assignment_name 
		this._userService.Register(this.user,this.clave_assignment).subscribe(
			response=>{
				if(response.user && response.user._id){
					this.user = response.user;
					this.assignment_name=this.user.assignment_name
					this.message ='success';
					 if(!this.filesToUpload){
			            this._router.navigate(['/index']);
			          }else{
			            //Subir archivo
			            this._uploadService.makeFileRequest(this.url+'uploadImage/'+this.user._id,[],this.filesToUpload,'image') //AGREGAR TOKEN
			                .then((result:any)=>{
			                  this.user.image=result.image;
			                  this._assignmentService.getInfoAssignment(this.assignment_name).subscribe(
			                  	response=>{
			                  		this.assignment=response.assignment;
			                  		if(!this.filesToUploadCatalogProfesor){
							            this._router.navigate(['/index']);
							          }else{
							            //Subir archivo
							            this._uploadService.makeFileRequest(this.url+'uploadCatalogProfesor/'+this.assignment._id,[],this.filesToUploadCatalogProfesor,'catalog_profesor') 
							                .then((result:any)=>{
							                  this.assignment.catalog_profesor=result.catalog_profesor;	
							                  this._assignmentService.registerCatalogProfesor(this.assignment._id,this.assignment.catalog_profesor).subscribe(
							                  		response=>{
							                  				this._router.navigate(['/login']);
							                  			},error=>{
							                  				console.log(<any>error);
							                  			}
							                  	);		
							                  	this._router.navigate(['/login']);                  
							                });
							          }
					                  
					                });
			                  	},
			                  	error=>{
									console.log(<any>error);
			                  	});			                  
			          }
					this.user= new User('','','','','','','','','');
					registerForm.reset();
				}
				
			},
			error=>{
				console.log(<any>error);
			}
		);
	}
	fileChangeEvent(fileInput:any){
   	 this.filesToUpload= <Array<File>>fileInput.target.files;
  	}
  	fileChangeEventCatalogProfesor(fileInput:any){
   	 this.filesToUploadCatalogProfesor= <Array<File>>fileInput.target.files;
  	}
}


