import {Component,OnInit} from '@angular/core';
import {Router,ActivatedRoute,Params} from '@angular/router';
import { User } from'../../models/user';
import {GLOBAL} from '../../services/global';
import {UserService} from '../../services/user.service';


@Component({
	selector:'updatePassword',
	templateUrl:'./updatePassword.component.html',
	styleUrls: ['./updatePassword.component.css'],
	providers: [UserService]
})

export class UpdatePasswordComponent implements OnInit{
	public title:String;
	public user:User;
	public message:string;
	public url:string;
	public assignment_name:string;
	public identity;



	constructor(
		private _route:ActivatedRoute,
		private _router:Router,
		private _userService:UserService

	){
		this.title='Update password';
		this.identity=this._userService.getIdentity();
		this.user= new User('','','',this.identity.assignment,'','','','','');
		this.url=GLOBAL.url;
	}

	ngOnInit(){
		console.log('Registro cargado');
	}
	onSubmit(updatePasswordForm){
		this._userService.updatePassword(this.user.assignment,this.user.password).subscribe(
			response=>{
				if(response.user){
					this.user=response.user;
					this._router.navigate(['/inicio']);	
				}else{
					console.log('error');
				}
				
			},
			error=>{
				console.log(<any>error);
			}
		);
	}
}


