import {Component,OnInit} from '@angular/core';
import {Router,ActivatedRoute,Params} from '@angular/router';
import { User } from'../../models/user';
import {GLOBAL} from '../../services/global';
import {UserService} from '../../services/user.service';
import {UploadService} from '../../services/upload.service';


@Component({
	selector:'mydata',
	templateUrl:'./mydata.component.html',
	styleUrls: ['./mydata.component.css'],
	providers: [UserService,UploadService]
})

export class MyDataComponent implements OnInit{
	public title:String;
	public user:User;
	public message:string;
	public filesToUpload: Array<File>;
	public url:string;
	public assignment_name:string;
	public identity;
	public token;



	constructor(
		private _route:ActivatedRoute,
		private _router:Router,
		private _userService:UserService,
		private _uploadService:UploadService

	){
		this.title='Upload data';
		this.user= new User('','','','','','','','','');
		this.url=GLOBAL.url;
		this.identity=this._userService.getIdentity();
		this.token=this._userService.getToken();

	}

	ngOnInit(){
		
	}
	onSubmit(mydataForm){

					 if(!this.filesToUpload){
			            this._router.navigate(['/index']);
			          }else{
			            this._uploadService.makeFileRequest(this.url+'uploadNewImage/'+this.identity._id+'/'+this.identity.image,[],this.filesToUpload,'image') //AGREGAR TOKEN
			                .then((result:any)=>{
			                  this.identity.image=result.image;
			                  this.message='success';
			                  window.location.reload();
			                  this._router.navigate(['/inicio']);
			                });
			          }
					this.user= new User('','','','','','','','','');
					this._router.navigate(['/inicio']);	
	}
	fileChangeEvent(fileInput:any){
   	 this.filesToUpload= <Array<File>>fileInput.target.files;
  	}
}


