import { Component,OnInit,Input} from '@angular/core';
import { Router,ActivatedRoute,Params } from '@angular/router';
import { Assignment } from'../../models/assignment';
import {AssignmentService} from '../../services/assignment.service';
import { User } from'../../models/user';
import {UserService} from '../../services/user.service';
import {TopicService} from '../../services/topic.service';
import { Topic } from'../../models/topic';
import {SubtopicService} from '../../services/subtopic.service';
import { Subtopic } from'../../models/subtopic';
import {MaterialService} from '../../services/material.service';
import { Material } from'../../models/material';
import {UploadService} from '../../services/upload.service';
import {ProfesorService} from '../../services/profesor.service';
import {GLOBAL} from '../../services/global';


@Component({
  selector: 'addmaterial',
  templateUrl: './addmaterial.component.html',
  styleUrls: ['./addmaterial.component.css'],
  providers: [AssignmentService,UserService,TopicService,SubtopicService,MaterialService,UploadService,ProfesorService]
})
export class AddMaterialComponent implements OnInit{
 
  public title:String;
  public material:Material;
  public assignment;
  public topics:Topic[];
  public subtopics:Subtopic[];
  public message;
  public filesToUpload: Array<File>;
  public url:string;
  public template;
  public template_select;
  public profesors;
  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _assignmentService:AssignmentService,
    private _userService:UserService,
    private _topicService:TopicService,
    private _subtopicService:SubtopicService,
    private _materialService:MaterialService,
    private _uploadService:UploadService,
    private _profesorService:ProfesorService


  ){
    this.title='Asignaturas';
    this.material= new Material('','','','','','','','','');
    this.url=GLOBAL.url;
  }
  ngOnInit(){
       var l = [];
      this.assignment=this._assignmentService.getAssignment();
      //console.log(this.assignment);
      this._topicService.getInfoTopics(this.assignment).subscribe(//AGREGAR TOKEN
        response=>{
            this.topics=response.topics;
            for (let t in this.topics) {
                this._subtopicService.getInfoSubtopics(this.topics[t]._id).subscribe(
                 response=>{
                      l=l.concat(response.subtopics);
                      this.subtopics=l;
                   },
                    error=>{
                     console.log(<any>error);
                   }
                );
            }
            this._profesorService.getProfesors(this.assignment).subscribe(
                response=>{
                    this.profesors=response.profesors;
                  },
                  error=>{
                    console.log(<any>error);
                  }
            );
          },
          error=>{
            console.log(<any>error);
          }
      );

    /*Se obtiene el template seleccionado para que se vea el HTML correspondiente */ 
    this.template=this._assignmentService.getTemplate();
    if(this.template!=null){
      this._assignmentService.getInfoAssignment(this.template.assignment_name).subscribe(
        response=>{
          this.template_select = response.assignment.template_select;
        },
        error=>{
          console.log(<any>error);
        }
      );
    }//fin IF
  }

  onSubmit(materialForm){
    this.message='error';
    setTimeout(()=>{ 
      this.message='wait';  
    },3000);

    this._materialService.AddMaterial(this.material).subscribe(
      response=>{
        if(response.material && response.material._id){
          this.material = response.material;
          this.message ='success';
          if(!this.filesToUpload){
            this._router.navigate(['/inicio']);
          }else{
            //Subir archivo
            this._uploadService.makeFileRequest(this.url+'uploadFile/'+this.material._id,[],this.filesToUpload,'file') //AGREGAR TOKEN
                .then((result:any)=>{
                  this.material.file=result.file;
                  setTimeout(()=>{ 
                    this._router.navigate(['/inicio']);  
                  },3000);
                });
          }
          this.material= new Material('','','','','','','','','');
          materialForm.reset();
        }
      },
      error=>{
        console.log(<any>error);
      }
    );

  }
  
  fileChangeEvent(fileInput:any){
    this.filesToUpload= <Array<File>>fileInput.target.files;
  }
}



