import {Component,OnInit} from '@angular/core';
import {Router,ActivatedRoute,Params} from '@angular/router';
import { User } from'../../models/user';
import {GLOBAL} from '../../services/global';
import {UserService} from '../../services/user.service';
import {UploadService} from '../../services/upload.service';
import {AssignmentService} from '../../services/assignment.service';


@Component({
	selector:'updateProfesors',
	templateUrl:'./updateProfesors.component.html',
	styleUrls: ['./updateProfesors.component.css'],
	providers: [UserService,UploadService,AssignmentService]
})

export class UpdateProfesorsComponent implements OnInit{
	public title:String;
	public user:User;
	public message:string;
	public filesToUploadCatalogProfesor: Array<File>;
	public url:string;
	public identity;
	public assignment;
	public assignmentBody;
	public path;


	constructor(
		private _route:ActivatedRoute,
		private _router:Router,
		private _userService:UserService,
		private _uploadService:UploadService,
		private _assignmentService:AssignmentService

	){
		this.url=GLOBAL.url;
		this.identity=this._userService.getIdentity();
	}

	ngOnInit(){
		this.assignment=this.identity.assignment;
		this._assignmentService.getInfoAssignment(this.identity.assignment_name).subscribe(
			response=>{
					this.assignmentBody=response.assignment;
				},error=>{
					console.log(<any>error);
				}
		);

	}
	onSubmit(updateProfesorsForm){
		this.path=this.assignmentBody.catalog_profesor;
		if(!this.filesToUploadCatalogProfesor){
			this._router.navigate(['/inicio']);
		}else{/*
			this._uploadService.makeFileRequest(this.url+'updateCatalogProfesors/'+this.assignment+'/'+this.path,[],this.filesToUploadCatalogProfesor,'catalog_profesor') 
				.then((result:any)=>{
					this.path=result.catalog_profesor;
					this._assignmentService.updateCatalogProfesorsCommit(this.assignment,this.path).subscribe(
							response=>{
								this._router.navigate(['/inicio']);
							},error=>{

							}
						);
					this._router.navigate(['/inicio']);
					//window.location.reload();                  
				});*/
		}
	}
	fileChangeEventCatalogProfesor(fileInput:any){
   	 this.filesToUploadCatalogProfesor= <Array<File>>fileInput.target.files;
  	}
  	
}


