import { Injectable } from '@angular/core';
import { Http, Response, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';

@Injectable()
export class RequestService{
	public url:string;

	constructor(private _http:Http){
		this.url=GLOBAL.url;
	}
	AddRequest(request_to_add){
		let params = JSON.stringify(request_to_add);
		let headers = new Headers({'Content-Type':'application/json'});
		return this._http.post(this.url+'saveRequest',params,{headers:headers})
						 .map(res=>res.json());
	}
	ViewRequest(assignment_id){
		return this._http.get(this.url+'getRequests/'+assignment_id).map(res=>res.json());
	} 

	updateRequest(request_id,newStatus,token) {
		let headers = new Headers({'Content-Type':'application/json', 'Authorization': token});
		return this._http.put(this.url + 'updateRequest/' + request_id + '/' + newStatus,'',{headers:headers}).map(res => res.json());
	}
	
}


