import { Component,OnInit } from '@angular/core';
import { Router,ActivatedRoute,Params } from '@angular/router';
import { User } from'../../models/user';
import {GLOBAL} from '../../services/global';
import {UserService} from '../../services/user.service';
import {TemplateService} from '../../services/template.service';
import { Template } from'../../models/template';
import {AssignmentService} from '../../services/assignment.service';

@Component({
	selector:'login',
	templateUrl:'./login.component.html',
	styleUrls: ['./login.component.css'],
	providers: [UserService,TemplateService,AssignmentService]
})

export class LoginComponent implements OnInit{
	public title:String;
	public user:User;
	public identity;
	public token;
	public status:string;
	public template;
	public template_select;

	constructor(
		private _route:ActivatedRoute,
		private _router:Router,
		private _userService:UserService,
		private _templateService:TemplateService,
		private _assignmentService:AssignmentService
	){
		this.title='Login';
		this.user= new User('','','','','','','','','');
		

	}

	ngOnInit(){
		//Si el usuario ya esta logeado
		if (this._userService.getIdentity() != null){
			this._router.navigate(['/inicio']);
		}

		/*Se obtiene el template seleccionado para que se vea el HTML correspondiente */ 
		this.template=this._assignmentService.getTemplate();
		if(this.template!=null){
		  this._assignmentService.getInfoAssignment(this.template.assignment_name).subscribe(
			response=>{
			  this.template_select = response.assignment.template_select;
			  //console.log(this.template_select);
			},
			error=>{
			  console.log(<any>error);
			}
		  );
		}//fin IF

	}

	onSubmit(){
		this._userService.Login(this.user).subscribe(
			//Datos del usuario
			response=>{
				this.identity=response.user;
				if(!this.identity || !this.identity._id){
					alert('usuario login no correctamente');
				}else{
					this.identity.password='';
					//this.identity.assignment
					this._templateService.getInfoTemplate(this.identity.assignment).subscribe(
	     				  response=>{
	           				 this.template=response.assignment;
	           				// console.log(this.template);
	           				localStorage.removeItem('template');
	            			localStorage.removeItem('assignment');
	            			//localstorage para la materia y el template
	            			localStorage.setItem('template',JSON.stringify(this.template));
							localStorage.setItem('assignment',this.identity.assignment);
	         				 },
	          				error=>{
	           						 console.log(<any>error);
	         				 }
					  );
					  
					localStorage.setItem('identity',JSON.stringify(this.identity));
					//Conseguir token
					this._userService.Login(this.user,'true').subscribe(
						response=>{
							this.token=response.token;
							
							if(this.token.length <= 0){
								alert('token no generado');
							}else{
								localStorage.setItem('token',this.token);
								this.status='wait';
								//redireccinamiento cuando inicio sesion
								setTimeout(()=>{ 
									this.status='success';
								  },3000);
								  setTimeout(()=>{ 
									this.status='pause';
									window.location.reload();
								this._router.navigate(['/inicio']);
								  },2000);
							}
						},
						error=>{

							console.log(<any>error);
						}
					);
				}
			},
			error=>{
				var errorMensaje=<any>error;
				if(errorMensaje != null){
					var body = JSON.parse(error._body);
					this.status='wait';
					setTimeout(()=>{ 
						this.status='error';
					  },2000);
					  setTimeout(()=>{ 
						this.status='pause';
					  },5000);
				}
			}
		);
	}

}


