import { Injectable } from '@angular/core';
import { Http, Response, Headers,RequestOptions} from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';
@Injectable()
export class AssignmentService{
	public url:string;
	public template;
	public assignment;

	constructor(private _http:Http){
		this.url=GLOBAL.url;
	}
	
	getInfoAssignments(){
		return this._http.get('getInfoAssignments/').map(res=>res.json());
	}
	getInfoAssignment(assignment_name){
		return this._http.get('getInfoAssignment/'+assignment_name).map(res=>res.json());
	}
	getTemplate(){
			let template=JSON.parse(localStorage.getItem('template'));
			if(template!="undefined"){
				this.template = template;
			}else{
				this.template = null;
			}
			return this.template;
		}

	getAssignment(){
		let assignment=localStorage.getItem('assignment');
		if(assignment!="undefined"){
			this.assignment = assignment;
		}else{
			this.assignment = null;
		}
		return this.assignment;
	}


	updateAssignment(assignment_id,tempSelect,token) {
		let headers = new Headers({'Content-Type':'application/json', 'Authorization': token});
		let par =  { 'template_select' : ''	}
		par.template_select = tempSelect;
		let params = JSON.stringify(par);
		return this._http.put('update_assignment/' + assignment_id,params,{headers:headers}).map(res => res.json());
	}

	//NUEVO
	registerCatalogProfesor(assignment,path){
		let params='';
		let headers = new Headers({'Content-Type':'application/json'});
		return this._http.post('updateCatalogProfesorsCommit/'+assignment+'/'+path,params,{headers:headers}).map(res=>res.json());
	}

}




