import { Injectable } from '@angular/core';
import { Http, Response, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {GLOBAL} from './global';

@Injectable()
export class UserService{
	public url:string;
	public identity;
	public token;

	constructor(private _http:Http){
		this.url=GLOBAL.url;
	}

	Register(user_to_register,clave_assignment){
		let clave = {'clave_assignment' : clave_assignment};
		let join = {};
		Object.assign(join,user_to_register,clave);
		let params = JSON.stringify(join);
		let headers = new Headers({'Content-Type':'application/json'});
		return this._http.post(this.url+'register',params,{headers:headers})
						 .map(res=>res.json());
	}
	Login(user_to_login , gettoken=null){
		if(gettoken != null){
			user_to_login.gettoken = gettoken;
		}
		let params = JSON.stringify(user_to_login);
		let headers = new Headers({'Content-Type':'application/json'});
		return this._http.post(this.url+'login',params,{headers:headers})
						 .map(res=>res.json());
	}
	getIdentity(){
		let identity=JSON.parse(localStorage.getItem('identity'));
		if(identity!="undefined"){
			this.identity = identity;
		}else{
			this.identity = null;
		}
		return this.identity;
	}

	getToken(){
		let token=localStorage.getItem('token');
		if(token!="undefined"){
			this.token = token;
		}else{
			this.token = null;
		}
		return this.token;
	}

	getMaster(assignment_name){
		return this._http.get(this.url+'getMaster/'+assignment_name).map(res=>res.json());
	}
	Upload(user_to_update){
		let params = JSON.stringify(user_to_update);
		return this._http.put(this.url+'update_user/'+user_to_update._id,params);
	}

	updatePassword(id,password){
		let params = JSON.stringify(id,password);
		return this._http.put(this.url+'updatePassword/'+id+'/'+password,params)
						 .map(res => res.json());
	}
}


